* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

h1 {
  color: #ffffff;
  margin-left: 15px;
}

body {
  height: 100vh;
  background-color: #37306b;
}

header {
  position: relative;
  padding: 0 2rem;
}

.movie__banner {
  position: relative;
  margin: 30px auto;
  width: 100%;
  max-width: 1200px;
  height: 450px;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
  z-index: 1;
}
.movie__banner img {
  opacity: 40%;
}
.movie__banner .movie__banner-poster {
  position: absolute;
  width: 20%;
  height: 60%;
  top: 35%;
  left: 20px;
  z-index: 2;
  opacity: 1;
}
.movie__banner .movie__banner-poster img {
  opacity: 1;
}
.movie__banner .movie__banner-info {
  position: absolute;
  width: 73%;
  display: flex;
  justify-content: space-between;
  top: 80%;
  left: 25%;
  font-size: 20px;
  z-index: 2;
}

.movie__banner img {
  width: 100%;
  height: 100%;
  -o-object-fit: fill;
  object-fit: fill;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border-radius: 10px;
}

.load-more-button-text {
  display: none;
}

.movie__card-container {
  width: 100%;
  max-width: 1300px;
  overflow: hidden;
  margin: 0 auto 20px auto;
}

.movie__card-rows {
  width: 100%;
  max-width: 1250px;
  overflow: hidden;
  padding-left: 10px;
  padding-right: 10px;
  margin: 20px auto;
  position: relative;
}

.movie-card {
  list-style: none;
  display: flex;
  flex-direction: column;
  float: left;
  width: 21%;
  height: 170px;
  margin: 30px 2% 0px 2%;
  border-radius: 10px;
  position: relative;
}
.movie-card img {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 10px;
}
.movie-card .rating {
  position: absolute;
  bottom: -2px;
  right: 2px;
  display: inline-block;
  color: orange;
  line-height: 30px;
  text-align: center;
}

.movie__card-rows .slide {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  background: transparent;
  color: white;
  margin-top: 80px;
  height: 70px;
  width: 34px;
  cursor: pointer;
  border-radius: 5px;
  margin-left: -12px;
}

.movie__card-rows .right {
  position: absolute;
  right: 0;
  margin-top: 80px;
  margin-right: -3px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  border-radius: 10px;
  transition: opacity 0.4s ease-in-out;
  background: black;
  cursor: pointer;
}

.load-more-button {
  display: none;
}

.movie-card:hover .overlay {
  opacity: 0.75;
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-family: Verdana;
  text-align: center;
}

@media (max-width: 768px) {
  .movie-card {
    width: 45%;
    height: 150px;
    margin: 60px 2.5%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  }
  .movie-card img {
    border-radius: 5%;
  }
  .movie__card-rows {
    width: 100%;
    max-width: 1200px;
    overflow: hidden;
    margin: 20px auto;
    position: relative;
    padding-bottom: 60px;
  }
  .load-more-button {
    position: relative;
    display: block;
    height: 30px;
    margin-top: 260px;
    padding: 10px 20px;
    background-color: #d27685;
    color: white;
    font-size: 18px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }
  .load-more-button .load-more-button-text {
    display: block !important;
    position: absolute;
    left: 37%;
    top: 17%;
  }
  .movie__card-rows .slide {
    display: none;
  }
  .movie__banner {
    display: none;
  }
  .overlay {
    display: none;
  }
  header {
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
  }
}